.modal-backdrop {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  /* padding-top: 100px; display: block; */
  background-color: black;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
}

.modal-content {
  width: 60%;
  min-height: 200px;
  background-color: white;
  padding: 25px;
}
