.neo-switch {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Roboto";
  color: #0174ff !important;
}
.neo-switch label {
  position: relative;
  display: inline-block;
  /* width: 9rem;
    height: 2.5rem; */
  width: 110px;
  height: 37px;
  cursor: pointer;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}
.neo-switch label:before {
  position: relative;
  top: 0px;
  left: 0px;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 2.5rem;
  /* box-shadow: -2px -4px 6px 0px rgba(255, 255, 255, 0.6) inset, 2px 4px 6px 0px rgba(136, 165, 191, .6) inset; */
  content: "";
  background: #8ada9c;
  transition: background 0.3s ease;
  /* border: 1px solid #0174FF !important */
}
.neo-switch label span {
  color: white;
  position: absolute;
  /* top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center; */
  /* width: 2.5rem;
    height: 2.5rem; */
  top: 1px;
  left: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background: linear-gradient(90deg, #e3ffe7 0%, #d9e7ff 100%);
  box-shadow: -2px -4px 6px 0px rgba(255, 255, 255, 0.6),
    2px 4px 6px 0px rgba(136, 165, 191, 0.6);
  transition: all 0.3s ease;
  background: #8ada9c;
  border: 1px solid #0174ff !important;
  background: #d9e7ff !important;
}

.neo-switch label svg {
  fill: none;
  -webkit-transform: scale(2);
          transform: scale(2);
}
.neo-switch label svg path {
  transition: all 0.3s linear;
  stroke: #eb2f06;
  stroke-dasharray: 24;
  stroke-dashoffset: 0;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: 2;
}
.neo-switch #toggle-input {
  display: none;
}
.neo-switch #toggle-input:checked + label span {
  -webkit-transform: translateX(4.3rem);
          transform: translateX(4.3rem);
}
.neo-switch #toggle-input:checked + label path {
  stroke: #8ada9c;
  stroke-dasharray: 25;
  stroke-dashoffset: 25;
}
#toggle-input-data .knobs-data:before {
  content: "Active";
}

.neo-switch .active {
  position: absolute;
  right: 34px;
  font-weight: 700;
  color: #0174ff !important;
}

.neo-switch .inactive {
  position: absolute;
  left: 40px;
  font-weight: 700;
  font-size: 18px;
  color: #0174ff !important;
}

