.table-responsiveMain{
    height: 50vh;
    overflow-y: auto;
    font-size: 14px;
}
.table-responsive .table{
 width: 100%;
 border-collapse:collapse;
}

.table-responsive .table td , th{
    /* padding: 10px; */
    text-align: center;
    padding-top: 5px;
    padding-bottom: 5px;
    /* border-right: none !important; */
   
}

.table-responsive .saarthiTableThead{
    background-color: #E6EAF8;
    padding: 5px;
}
.table-responsive .table th{
   /* border-right: 1px solid  white; */
  
}
.table-responsive .table .outerDiv{
    display: flex;
    justify-content: space-between;
}
.table-responsive .table .borderDiv{
    border: 1px solid #ffffff;
}
.table-responsive .table .titleName{
    padding: 0px 40px;
    /* border-right: 2px solid red; */
}

.table-responsive .table{
 width: 100%;
}

.table-responsive .table td , th{
    padding: 0px;
    min-width: auto !important;
    text-align: center;
    padding-top: 5px;
    padding-bottom: 5px;
    border-right: 0.59559px solid #d8d8d8;
    /* border-right: none !important;
    border-left: none !important; */
}
.table-responsive .table th{
    padding-top: 10px;
    padding-bottom: 10px;
    /* border-right: 0.59559px solid #d8d8d8; */
    /* border-right: none !important;
    border-left: none !important; */
}
.table-responsive .table thead{
    position: sticky;
    top:0px;
    z-index: 5;
    
}
.table-responsive .table thead tr{
    border-top: 0.59559px solid #d8d8d8;
    border-bottom: 0.59559px solid #d8d8d8;
}
/* .table-responsive .table thead tr th{
    border-right: 0.59559px solid #d8d8d8;
}
.table-responsive .table thead tr th{
    border-right: 0.59559px solid #d8d8d8; 
}*/

.table-responsive .table tbody tr{
    border-bottom: 0.59559px solid #d8d8d8;
}
.table-responsive .table thead tr >th:nth-child(6) {
    width: auto !important;
}

.table-responsive .table thead tr >th:nth-child(5) {
    width: auto !important;
}

.table-responsive .table thead tr >th:nth-child(1) {
    width: auto !important;
}

.table-responsive .table thead tr >th:nth-child(3) {
    width: auto !important;
}
.table-responsive .table thead tr>th:first-child, .table thead tr>th:nth-child(3) {
    width: auto !important;
}

.table-responsive .table thead tr >th:nth-child(4) {
    width: auto !important;
}


.table-responsive .saarthiTableThead{
    /* background-color: #E6EAF8; */
    background-color: #DDECFF;
}
